<template lang="html">
  <div class="container-fulid">
      <!-- tag箭头 -->
      <div class="pb_tag_top">
        <span><a href="javascript:history.go(-1)" class="back"><img src="@/assets/images/arrow-lift.png"></a></span>
          我的
      </div>
      <div class="pb_top_zhanwei"></div>

      <div class="container-fluid my_cont">
          <div class="my_cont_1">
            <img src="@/assets/images/my1.png">
            <div class="my_ct_pos">
                <div class="pos_m_a fl">
                  <img src="@/assets/images/5959.png" alt="">
                </div>
                <div class="pos_m_b fl">
                    <h1>{{this.username}}</h1>
                    <button>个人中心</button>
                </div>
            </div>
          </div>

          <div class="my_cont_2 container">
            <ul>
              <li @click="hdjilu()"><img src="@/assets/images/sm1.png"> <span>活动记录</span>  <dbo><img  src="@/assets/images/rscopy.png"></dbo> </li>
              <li @click="myinfo()"><img src="@/assets/images/sm2.png"> <span>我的消息</span>  <dbo><img  src="@/assets/images/rscopy.png"></dbo> </li>
              <li @click="lianxi()"><img src="@/assets/images/sm3.png"> <span>联系我们</span>  <dbo><img  src="@/assets/images/rscopy.png"></dbo> </li>
              <li @click="shezhi()"><img src="@/assets/images/sm4.png"> <span>设置</span>  <dbo><img  src="@/assets/images/rscopy.png"></dbo> </li>
            </ul>
          </div>
      </div>

      <!-- <m-footer></m-footer> -->

    </div>
</template>

<script>

export default {
  data() {
    return {
      username:localStorage.getItem("user_name"),
    }
  },
  created(){

  },
  mounted() {

  },
  methods: {
    lianxi(){
      this.$router.push({
          name:'contactus'
        })
    },

    hdjilu(){
      this.$router.push({
          name:'activityrecord'
        })
    },
    myinfo(){
      this.$router.push({
          name:'myinfo'
        })
    },
    shezhi(){
      this.$router.push({
          name:'shezhi'
        })
    },
    // 获取企业名称

  }

}
</script>

<style lang="css" scoped>
</style>



